@use "sass:math";

@import "normalize";
@import "base";

@media screen {
  @import "screen";
}

@media screen and (min-width: 768px) {
  @import "tablet";
}

@media screen and (min-width: 1024px) {
  @import "desktop";
}

@media screen and (min-width: 1280px) {
  @import "largedesktop";
}

@media screen and (-ms-high-contrast: none), screen and (-ms-high-contrast: active) {
  .cutouttitle {
    background: transparent;
  }
}
